<template>
  <div class="home">
    <el-tabs type="border-card">
      <el-tab-pane label="代理">
        <el-row class="input" v-show="state==0">
          <el-input type="textarea" :rows="20" style="width:45%;float:left;" v-model="request" />
          <el-input type="textarea" :rows="20" style="width:45%;float:right;" v-model="response" />
        </el-row>
        <el-row v-show="state==0">
          <el-button type="primary" @click="setProxy">开启代理</el-button>
          <el-button type="primary" @click="forward">放包</el-button>
          <el-button type="primary" @click="stopProxy">停止代理</el-button>
          <el-button type="primary" @click="test">测试按钮</el-button>
        </el-row>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
// @ is an alias to /src
import Page from "@/components/Page.vue";

export default {
  name: "Home",
  components: {
    Page
  },
  methods: {
    setProxy: function() {
      eel.beginListening();
      this.query_task = setInterval(this.queryData, 1000);
      this.update_request_data = true;
    },
    queryData: async function() {
      if (this.update_request_data) {
        this.buf=await eel.queryData()();
        console.log("query once");
        if (this.buf) {
          this.request = this.buf.replace('keep-alive','close');
          this.update_request_data = false;
          if (this.state != 0) {
            this.$notify({
              title: "一个新的包被捕获",
              dangerouslyUserHTMLString: true,
              message:
                "" +
                this.request
                  .split("\n")
                  .slice(0, 3)
                  .join("\n")
            });
          }
          clearInterval(this.query_task);
          this.query_task = 0;
        }
      }
    },
    forward: async function() {
      console.log("send to server");
      this.response=await eel.sendDataToServer(
        this.request,
        1
      )();
      console.log("send to client");
      this.update_request_data = true;
      this.query_task = setInterval(this.queryData, 1000);
    },
    stopProxy: function() {
      if (this.query_task) clearInterval(this.query_task);
      eel.endListening();
    },
    test: function() {
      this.$notify({
        title: "一个新的包被捕获",
        dangerouslyUseHTMLString: true,
        message:
          "<strong>" +
          this.request
            .split("\n")
            .slice(0, 3)
            .join("<br>") +
          "</strong>"
      });
    }
  },
  data() {
    return {
      state: 0,
      request: "",
      response: "",
      buf: "",
      update_request_data: false,
      query_task: 0
    };
  }
};
</script>

